import PropTypes from 'prop-types';
import * as Yup from 'yup';
import { useEffect, useMemo } from 'react';
import { useSnackbar } from 'notistack';
import { useNavigate } from 'react-router-dom';
// form
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
// @mui
import { LoadingButton } from '@mui/lab';
import { Box, Card, Grid, Stack, Typography } from '@mui/material';

// routes
import { PATH_ADMIN } from '../../routes/paths';
import { FormProvider, RHFSelect, RHFSwitch, RHFTextField } from '../../components/hook-form';

import axios from '../../utils/axios';
// ----------------------------------------------------------------------

CreateCablePlan.propTypes = {
  isEdit: PropTypes.bool,
  currentUser: PropTypes.object,
};
export default function CreateCablePlan({ isEdit, currentUser }) {
  const { enqueueSnackbar } = useSnackbar();
  const cablename = [
    'DSTV',
    'GOTV',
    'STARTIME',
    'SHOWMAX'
  ];
  const NewUserSchema = Yup.object().shape({
    plan_name: Yup.string().required('Plan Name Required'),
    plan_price: Yup.string().required('Plan Price Required'),
    cable_name: Yup.string().required('Cable Name Required')
  });
  const defaultValues = useMemo(
    () => ({
      cable_name: currentUser?.cable_name || '',
      plan_name: currentUser?.plan_name || '',
      plan_price: currentUser?.plan_price || '',
      plan_status: currentUser?.plan_status || false,
      plan_id: currentUser?.plan_id || '',
      Habukhan1: currentUser?.Habukhan1 || '',
      Habukhan2: currentUser?.Habukhan2 || '',
      Habukhan3: currentUser?.Habukhan3 || '',
      Habukhan4: currentUser?.Habukhan4 || '',
      Habukhan5: currentUser?.Habukhan5 || '',
      vtpass: currentUser?.vtpass || '',
      autopilot: currentUser?.autopilot || ''
    }),
    [currentUser]
  );
  const AccessToken = window.localStorage.getItem('accessToken');
  const navigate = useNavigate();
  const methods = useForm({
    resolver: yupResolver(NewUserSchema),
    defaultValues,
  });

  const {
    reset,
    watch,
    handleSubmit,
    formState: { isSubmitting },
  } = methods;
  useEffect(() => {
    if (isEdit && currentUser !== undefined) {
      reset(defaultValues);
    }
    if (!isEdit) {
      reset(defaultValues);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isEdit, currentUser]);
  const values = watch();
  const onSubmit = async (data) => {
    try {
      await axios.post(`/api/add/cable/plan/new/habukhan/safe/url/${AccessToken}/secure`, data);
      enqueueSnackbar('Create success!');
      navigate(PATH_ADMIN.plan.cable);
    } catch (error) {
      if (error.data?.message !== undefined) {
        enqueueSnackbar(error.data?.message, { variant: 'error' })
      } else if (error?.message !== undefined) {
        enqueueSnackbar(error?.message, { variant: 'error' })
      } else {
        enqueueSnackbar('Something Went Wrong', { variant: 'error' })
      }
    }
  }
  const onCreateReset = async (data) => {
    try {
      await axios.post(`/api/add/cable/plan/new/habukhan/safe/url/${AccessToken}/secure`, data);
      reset();
      enqueueSnackbar('Create success!');
    } catch (error) {
      if (error.data?.message !== undefined) {
        enqueueSnackbar(error.data?.message, { variant: 'error' })
      } else if (error?.message !== undefined) {
        enqueueSnackbar(error?.message, { variant: 'error' })
      } else {
        enqueueSnackbar('Something Went Wrong', { variant: 'error' })
      }
    }
  }

  const onEdit = async (data) => {
    try {
      await axios.post(`/api/system/admin/edit/cableplan/cableplan/habukhan/secure/boss/asd/asd/changes/${AccessToken}/secure`, data)
      navigate(PATH_ADMIN.plan.cable);
      enqueueSnackbar('Update success!');
    } catch (error) {
      if (error.data?.message !== undefined) {
        enqueueSnackbar(error.data?.message, { variant: 'error' })
      } else if (error?.message !== undefined) {
        enqueueSnackbar(error?.message, { variant: 'error' })
      } else {
        enqueueSnackbar('Something Went Wrong', { variant: 'error' })
      }
    }
  }

  const onEditReset = async (data) => {
    try {
      await axios.post(`/api/system/admin/edit/cableplan/cableplan/habukhan/secure/boss/asd/asd/changes/${AccessToken}/secure`, data)
      navigate(PATH_ADMIN.plan.newcable);
      enqueueSnackbar('Update success!');
    } catch (error) {
      if (error.data?.message !== undefined) {
        enqueueSnackbar(error.data?.message, { variant: 'error' })
      } else if (error?.message !== undefined) {
        enqueueSnackbar(error?.message, { variant: 'error' })
      } else {
        enqueueSnackbar('Something Went Wrong', { variant: 'error' })
      }
    }
  }


  return (
    <FormProvider methods={methods} onSubmit={handleSubmit(!isEdit ? onSubmit : onEdit)}>
      <Grid container spacing={3}>
        <Grid item xs={12} md={8}>
          <Card sx={{ p: 3 }}>
            <Box
              sx={{
                display: 'grid',
                columnGap: 2,
                rowGap: 3,
                gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(2, 1fr)' },
              }}
            >
              <RHFSelect name="cable_name" label="Cable Name" placeholder="Network" defaultValue="DSTV">
                <option value="" />
                {cablename?.map((option) => (
                  <option key={option} value={option}>
                    {option}
                  </option>
                ))}
              </RHFSelect>
              <RHFTextField name="plan_name" label="Plan Name" />
              <RHFTextField name="plan_price" label="Plan Price" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }} />              <RHFSwitch
                name="plan_status"
                labelPlacement="start"
                label={
                  <>
                    <Typography variant="subtitle2" sx={{ mb: 0.5 }}>
                      {values.plan_status ? 'ACTIVE' : 'DEACTIVATED'}
                    </Typography>
                  </>
                }

                sx={{ mx: 0, width: 1, justifyContent: 'space-between' }}
              />

              <RHFTextField name="Habukhan1" label="Habukhan 1 PLAN ID" />
              <RHFTextField name="Habukhan2" label="Habukhan 2 PLAN ID" />
              <RHFTextField name="Habukhan3" label="Habukhan 3 PLAN ID" />
              <RHFTextField name="Habukhan4" label="Habukhan 4 PLAN ID" />
              <RHFTextField name="Habukhan5" label="Habukhan 5 PLAN ID" />

              <RHFTextField name="vtpass" label="VTPASS PLAN ID" />
              <RHFTextField name="autopilot" label="AUTOPILOT PLAN ID" />
            </Box>

            <Stack alignItems="flex-end" sx={{
              mt: 2, display: 'grid',
              columnGap: 1,
              rowGap: 2, gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(2, 1fr)' },
            }}>

              <LoadingButton type="submit" variant="contained" loading={isSubmitting}>
                {!isEdit ? 'Create then Return' : 'Save Changes then Return'}
              </LoadingButton>
              <LoadingButton type="button" onClick={handleSubmit(!isEdit ? onCreateReset : onEditReset)} variant="contained" color="warning" loading={isSubmitting}>
                {!isEdit ? ' Create then Reset' : 'Save Changes then Create New'}
              </LoadingButton>
            </Stack>
          </Card>
        </Grid>
      </Grid>
    </FormProvider>
  );
}
